html * {
    margin: 0;
    padding: 0;
    box-sizing: 0;
}
div.picture{
    width: 40%;
    height: 40vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
p{
    font-size: 1.5rem;
    margin-left: 3rem;
    margin-right: 3rem;
}
.callButton{
    color:white;
    font-size: 20px;
    border-radius: 24px;
    padding: 8px 16px;
    background: rgb(235,84,1);
    background: linear-gradient(100deg, rgba(235,84,1,1) 0%, rgba(1,106,102,1) 100%);
}
h2{
    color: #F6EBF9;
    background-color: rgba(0, 0, 0, 0.5);
    flex:1;
    text-align: center;
}
h3{
    color: #018C96;
    text-align: center;
    padding-top: 3rem;
}
div.myCarousel{
    min-height: 100vh;
    width: 100%;
    padding: 3rem 0rem;
    background-color:#F6EBF9;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 1.5rem 3%;
}
div.notFound{
    width: 100%;
    height: 80vh;
    background-color:#F6EBF9;
    display: flex;
    justify-content: center;
    align-items: center;
}
div.notFound p {
    font-size: 3rem;
    color: #016A66;
}
video{
    position: absolute;
    width: 100%;
    top: -100px;
    left: 0;
    visibility: visible;
    z-index: -10;
}
div.hero{
    width: 100%;
    min-height: 65vh;
    padding: 1rem 0;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}
div.navigator, div.footer{
    background: rgb(235,84,1);
    background: linear-gradient(100deg, rgba(235,84,1,1) 0%, rgba(1,106,102,1) 100%);
    width: 100%;
    min-height: 10vh;
    padding-left: 3rem;
    padding-right: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
div.footer{
    padding-right: 2.5rem;
}
.carousel-control-prev, .carousel-control-next {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45vh;
    margin-left: 1%;
    margin-right: 1%;
    width: 6vw !important;
    height: 6vw !important;
    border-radius: 100% 100%;
    background: rgb(235,84,1) !important;
    background: linear-gradient(100deg, rgba(235,84,1,1) 0%, rgba(1,106,102,1) 100%) !important;
}
ul li a, div a{
    text-decoration: none;
    background-color: transparent !important;
    color: white !important;
    font-size: 20px;
    margin: 0.5rem 0.5rem;
}
ul li div.dropdown-menu{
    background-color: brown;
    width: auto !important;
}
ul li div button a{
    color:#016A66 !important;
}
img#iconoHero{
    height: 8rem;
}
img.rrss{
    height: 2rem;
    margin: 0.5rem 0.5rem;
}
img.user{
    height: 40px;
}
@media screen and (max-width:992px) {
    div.hero{
        min-height: min-content;
        background-color: #F6EBF9;
        color:#016A66;
    }
    h3{
        padding-top: 0;
    }
}
@media screen and (max-width:768px) {
    div.picture{
        width: 85%;
        height: 20rem;
    }
    html{
        font-size: 10px;
    }
    .carousel-control-prev, .carousel-control-next {
        margin-top: 43.25rem;
        margin-left: 0.5rem;
        margin-right: 0.5rem;
        width: 4rem !important;
        height: 4rem !important;
    }
}